* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {

    justify-content: center;
    align-items: center;
}

h1 {
    text-align: center;
    margin-top: 4px;
}

input {

    border: 2px solid black;
    border-radius: 10px;
    height: 36px;
    margin: 10px 5px 25px 150px;
    width: 70%;

}

button {
    margin-left: 10px;
}


.card {
    display: block;
    padding: 3px;
    margin: 0px auto;
    width: 504px;
    height: 532px;
    background-color: skyblue;
    box-shadow: 8px 10px 37px 4px black;
}


span {
    color: white;
}

img {

    display: block;
    width: 170px;
    height: 173px;
    margin: auto;
}

.containerBox {
    width: 41%;
    display: block;
    margin: auto;
}